import Typography from 'typography';
import oceanBeach from 'typography-theme-ocean-beach';

oceanBeach.overrideThemeStyles = () => ({
  'h1,h2,h3,h4,h5,h6': {
    color: '#444',
    letterSpacing: '-0.05em',
  },
  strong: {
    letterSpacing: '-0.02em',
  },
  a: {
    backgroundImage: 'none',
    color: '#444',
    textDecoration: 'underline',
    textShadow: 'none',
    transition: 'color 0.2s ease-out',
  },
  'a:hover': {
    color: '#657986',
  },
  'a:focus': {
    outline: 'medium solid lightskyblue',
    outlineOffset: '4px',
  },
});

oceanBeach.googleFonts = [
  {
    name: 'Manrope',
    styles: ['400', '400i', '700', '700i'],
  },
];
oceanBeach.headerFontFamily = ['Manrope', 'san-serif'];
oceanBeach.bodyFontFamily = ['Manrope', 'san-serif'];

const typography = new Typography(oceanBeach);

export const { scale, rhythm, options } = typography;

export default typography;
